@import "../../_globalColor";

.project-card {
  position: relative;
  display: flex;
  flex-direction: column;
  background: linear-gradient(
    135deg,
    #ffffff,
    #fce8ee
  ); // soft version of light maroon
  border-radius: 20px;
  padding: 2rem;
  box-shadow: #e589aa;
  overflow: hidden;
  transition: transform 0.4s ease, box-shadow 0.4s ease;
  z-index: 1;
  border: 1px solid rgba(255, 255, 255, 0.1);
  box-shadow: 0 0 0 1px rgba(255, 255, 255, 0.1),
    0 8px 32px rgba(229, 137, 170, 0.3); // glowing maroon effect
  transition: transform 0.4s ease, box-shadow 0.4s ease;

  &:hover {
    transform: translateY(-12px) scale(1.015);
    box-shadow: 0 0 0 2px rgba(255, 255, 255, 0.2),
      0 20px 50px rgba(229, 137, 170, 0.4);
  }
}

.project-card:hover {
  transform: translateY(-12px) scale(1.015);
  box-shadow: 0 20px 50px rgba(229, 137, 170, 0.3); // light maroon glow
}

.project-card::before {
  content: "";
  position: absolute;
  top: -40px;
  right: -40px;
  width: 120px;
  height: 120px;
  background: radial-gradient(
    circle,
    #801336 0%,
    transparent 70%
  ); // deep maroon glow
  filter: blur(50px);
  z-index: 0;
}

.card-image {
  width: 100%;
  height: 200px;
  object-fit: cover;
  border-radius: 15px;
  margin-bottom: 1rem;
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.05);
  transition: transform 0.3s ease;
}

.project-card:hover .card-image {
  transform: scale(1.05);
}

.card-title {
  color: $textColor;
  font-size: 16px;
  font-weight: normal;
  text-align: center;
  margin-top: 1rem;
  margin-bottom: 0.5rem;
  background: none;
  animation: none;
  z-index: 1;
  text-shadow: none;
}
.card-subtitle {
  text-align: center;
  color: $textColor;
  font-size: 16px;
  margin-bottom: 1.5rem;
  animation: fadeInUp 0.6s ease-in-out;
}

.project-card-footer {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  gap: 0.5rem;
}

.project-tag {
  background: linear-gradient(90deg, #801336, #e589aa);
  color: white;
  font-size: 0.75rem;
  padding: 0.4rem 0.8rem;
  border-radius: 999px;
  transition: background 0.3s;
  cursor: pointer;
  box-shadow: 0 4px 12px rgba(229, 137, 170, 0.3);
}

.project-tag:hover {
  background: linear-gradient(90deg, #e589aa, #801336);
}

@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(15px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* Responsive Grid */
.projects-container {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
  gap: 2rem;
  padding: 2rem 0;
}

@media (max-width: 768px) {
  .card-title {
    font-size: 20px;
  }

  .card-subtitle {
    font-size: 14px;
  }
}
.skills-heading {
  font-size: 48px;
  font-weight: 800;
  text-align: left;
  margin-bottom: 1rem;
  color: #000; // Light mode (black)

  animation: fadeInUp 0.6s ease-in-out;

  @media (max-width: 768px) {
    font-size: 32px;
  }
}

// Dark mode override
.dark-mode .skills-heading {
  color: #fff; // Dark mode (white)
}
.project-card {
  background: none;
  box-shadow: none;
}

.card-title {
  color: $textColorDark;
}

.card-subtitle {
  color: $subTitle;
}

.project-tag {
  background: linear-gradient(90deg, #e589aa, #801336);
  color: #fff;

  &:hover {
    background: linear-gradient(90deg, #801336, #e589aa);
  }
}
